@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Quicksand', sans-serif;
}

.App-header {
  height: auto;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 3vmin);
  color: black;
  margin: 2vh auto 1vh auto;
}

.logo {
  max-height: calc(112px + 1vmin);
}

.header-text {
  font-weight: 900;
}

.App-body {
  margin: 1vh auto 3vh auto;
}

.container {
  margin: 1vh auto;
  max-width: 350px;
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: center;
}

.item {
  width: 50%;
  margin: auto;
}

/* washer images */
.item > div > img {
  max-width: 100%;
  overflow: hidden;
}

/* offline */
.offline-container {
  margin: 2vh 3vh;
}

.offline-cat {
  max-height: calc(180px + 5vmin);
  margin: 2vh auto;
  border-radius: 10px;
}

.offline-text {
  font-size: 1em,
}

